[mpk_tss_main] * [mol_string] {
	margin-top: 1px;
	padding: 0.45rem 1rem;
}

[mol_icon] {
	stroke: none;
}

[mpk_tss] {
	flex-direction: column;
	display: flex;
}

[mpk_tss_head] {
	display: flex;
	padding: 0;
	line-height: 2;
}

[mpk_tss_head] > [mol_button] {
	padding: 0 .5rem;
}

[mpk_tss_body] {
	flex: 1000 1 auto;
}

[mpk_tss_main_body_content] {
	height: 100%;
}

[mpk_tss_main_placeholder] {
	display: flex;
	align-items: center;
	justify-content: center;
}

[mpk_tss_main_foot_content] {
	justify-content: space-between;
}

[mpk_tss_main_foot_text] {
	max-width: 20rem;
}

[mpk_tss_main_logo] {
	color: white;
	box-shadow: none;
	height: 2.5rem;
	align-self: center;
	flex: none;
}

[mpk_tss_main_sidebar] {
	flex: 0 0 auto;
	background-color: var(--mol_theme_line);
}

[mpk_tss_main_sidebar_foot] {
	background-color: inherit;
	font-size: 0.9rem;
	color: var(--mol_theme_shade);
}

[mpk_tss_main_menu] {
	padding: .5rem 0 0;
	display: flex;
	flex-direction: column;
}

[mpk_tss_main_user_email] {
	margin-right: 0.5rem;
}

[mpk_tss_main_placeholder] {
	font-size: 1em;
	padding: 0;
	align-items: stretch;
	background: none;
	flex: 1000 1000 auto;
	position: relative;
	animation: none !important;
}

[mpk_tss_placeholder_head] {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    flex: 0 0 auto;
    position: relative;
    margin: 0;
    min-height: calc( 1.5em + 1rem );
    padding: .5rem;	
}

[mol_view][mpk_tss_placeholder_body] {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	padding: 1rem;
	background-color: rgba(238, 238, 238, 0.199);
}

[mpk_tss_placeholder_image] {
	width: 25%;
	box-shadow: none;
	opacity: .25;
}

[mpk_tss_placeholder_links] {
	flex: 0 0 auto;
	display: flex;
	flex-wrap: wrap;
}
